import './App.css';
import React, { Component } from 'react'
import QrReader from 'react-qr-reader'
class App extends Component {
  state = {
    result: ''
  }

  handleScan = data => {
    if (data) {
      this.setState({
        result: data
      })
    }
  }
  handleError = err => {
    console.error(err)
  }
  render() {
    return (
      <div>
        <div className="title">Scan Any QR Code</div>
        <QrReader
          delay={300}
          onError={this.handleError}
          onScan={this.handleScan}
          style={{ width: '100%' }}
        />
        {this.state.result && <p>QR code detected {this.state.result}</p>}
      </div>
    )
  }
}

export default App;
